
import AppHeader from '@/components/AppHeader.vue'; // @ is an alias to /src

import {
  defineComponent,
  ref,
  inject,
} from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default defineComponent({
  components: {
    AppHeader,
  },
  setup() {
    const router = useRouter();
    const axios: any = inject('axios');
    const store = useStore();
    const email = ref('');
    const password = ref('');
    const name = ref('');
    const confirmPassword = ref('');
    const emailBlured = ref(false);
    const nameBlured = ref(false);
    const passwordBlured = ref(false);
    const confirmPasswordBlured = ref(false);
    const valid = ref(false);
    const submitted = ref(false);

    const validEmail = (emailText: string) => {
      const re = /(.+)@(.+){2,}\.(.+){2,}/;
      return re.test(emailText.toLowerCase());
    };

    const validName = (nameText: string) => nameText.length >= 8 && nameText.length <= 200;

    const validPassword = (pass: string) => pass.length > 7;

    const validConfirmPassword = (pass: string) => pass === password.value;

    const validate = () => {
      emailBlured.value = true;
      nameBlured.value = true;
      passwordBlured.value = true;
      confirmPasswordBlured.value = true;
      if (validEmail(email.value) && validName(name.value)
      && validPassword(password.value) && validConfirmPassword(confirmPassword.value)) {
        valid.value = true;
      }
    };

    const submit = () => {
      validate();
      if (valid.value) {
        submitted.value = true;
        store.commit('setLoading', true);

        axios.post('/api/user/register', {
          userEmail: email.value,
          userName: name.value,
          userPassword: password.value,
        }).then((result: any) => {
          if (!result.handledByIntercepter) {
            const myResponse = result.data;
            store.commit('setLoading', false);

            if (myResponse.statusCode === 200) {
              store.commit('setShowGenericModal', true);
              store.commit('setGenericModalTitle', 'Register Successful');
              store.commit('setGenericModalMessage', 'You account is registed successfully. You may proceed to login to your account.');
              store.commit('setGenericModalCloseCallback', () => {
                router.push({ path: '/NotesAnywhere/' });
              });
            } else {
              store.commit('setShowGenericModal', true);
              store.commit('setGenericModalTitle', 'Register Failed');
              store.commit('setGenericModalMessage', myResponse.message);
              store.commit('setGenericModalCloseCallback', () => { /** */ });
            }
          }
        }).catch((err: any) => {
          store.commit('setLoading', false);
        });
      }
    };

    return {
      email,
      name,
      password,
      confirmPassword,
      emailBlured,
      nameBlured,
      passwordBlured,
      confirmPasswordBlured,
      valid,
      submitted,
      validEmail,
      validName,
      validPassword,
      validConfirmPassword,
      submit,
    };
  },
});

