
import {
  defineComponent,
  inject,
  reactive,
  watch,
  onMounted,
  onUnmounted,
  computed,
  ref,
} from 'vue';

import AppHeader from '@/components/AppHeader.vue'; // @ is an alias to /src
import TabViewer from '@/components/TabViewer.vue'; // @ is an alias to /src
import NotesPaper from '@/components/NotesPaper.vue'; // @ is an alias to /src
import NotePaperModel from '@/class/NotePaperModel';
import { useStore } from 'vuex';

export default defineComponent({
  components: {
    AppHeader,
    TabViewer,
    NotesPaper,
  },
  setup() {
    const store = useStore();
    const axios: any = inject('axios');
    const notesList = reactive([] as NotePaperModel[]);
    const boardBackground = ref();

    // watch notesList when changed, set dirty to true so that system will auto save.
    // watch(notesList, () => {
    //   store.commit('setIsDirty', true);
    //   store.commit('setAutoSaveCounter', 10);
    // });

    watch(() => store.state.selectedTab, (newValue, oldValue) => {
      if (store.state.selectedTab !== '') {
        axios.get(`/api/notes/get-all-notes?tabUniqueID=${store.state.selectedTab}`).then((result: any) => {
          if (!result.handledByIntercepter) {
            store.commit('setLoading', false);
            const myResponse = result.data;
            const myBoardBackground : HTMLElement = boardBackground.value as HTMLElement;
            const screenWidth = (window.innerWidth > 0) ? window.innerWidth : window.screen.width;
            const maxWidth = screenWidth < 900 ? 100 : 170;

            if (myResponse.statusCode === 200) {
              notesList.splice(0, notesList.length);
              myResponse.data.forEach((note: any, index: number, array: any) => {
                const maxID = notesList.reduce(
                  (accumulator, currentValue) => (currentValue.noteNo > accumulator
                    ? currentValue.noteNo : accumulator),
                  0,
                );

                let correspondingWidth = (note.noteWidth / note.screenWidth)
                * myBoardBackground.clientWidth;
                correspondingWidth = correspondingWidth > maxWidth ? correspondingWidth : maxWidth;
                // eslint-disable-next-line max-len
                const correspondingHeight = (note.noteHeight / note.screenHeight) * myBoardBackground.clientHeight;
                // eslint-disable-next-line max-len
                const correspondingNotePosX = (note.notePositionX / note.screenWidth) * myBoardBackground.clientWidth;
                // eslint-disable-next-line max-len
                const correspondingNotePosY = (note.notePositionY / note.screenHeight) * myBoardBackground.clientHeight;

                notesList.push(
                  new NotePaperModel(
                    maxID + 1,
                    note.tabUniqueID,
                    note.noteContent,
                    correspondingNotePosX,
                    correspondingNotePosY,
                    correspondingWidth,
                    correspondingHeight,
                    note.notePriority,
                    false,
                    myBoardBackground.clientWidth,
                    myBoardBackground.clientHeight,
                    note.notePaperUniqueID,
                  ),
                );

                if (index + 1 === array.length) {
                  setTimeout(() => {
                    store.commit('setIsDirty', false);
                    store.commit('setAutoSaveCounter', 0);
                  }, 200);
                }
              });
            } else {
              store.commit('setShowGenericModal', true);
              store.commit('setGenericModalTitle', 'Unexpected Error');
              store.commit('setGenericModalMessage', myResponse.message);
              store.commit('setGenericModalCloseCallback', () => { /** */ });
            }
          }
        }).catch((err: any) => {
          console.log(err);
          store.commit('setLoading', false);
          store.commit('setShowGenericModal', true);
          store.commit('setGenericModalTitle', 'Unexpected Error');
          store.commit('setGenericModalMessage', 'An unexpected error occured. Please contact administrator.');
          store.commit('setGenericModalCloseCallback', () => { /** */ });
        });
      } else {
        notesList.splice(0, notesList.length);
        store.commit('setIsDirty', false);
        store.commit('setAutoSaveCounter', 0);
      }
    });

    const updateNoteModel = (newNote: NotePaperModel, indexToUpdate: number) => {
      notesList[indexToUpdate] = newNote;
      store.commit('setIsDirty', true);
      store.commit('setAutoSaveCounter', 10);
    };

    const inactiveAllNote = () => {
      // eslint-disable-next-line no-param-reassign
      notesList.forEach((note: any) => { note.isActive = false; });
    };

    const changeActive = (clickedNote: NotePaperModel) => {
      // eslint-disable-next-line no-param-reassign
      notesList.forEach((note) => { note.isActive = false; });
      // eslint-disable-next-line no-param-reassign
      clickedNote.isActive = true;
    };

    const addNewNote = () => {
      const maxID = notesList.reduce(
        (accumulator, currentValue) => (currentValue.noteNo > accumulator
          ? currentValue.noteNo : accumulator),
        0,
      );
      // eslint-disable-next-line no-param-reassign
      notesList.forEach((note) => { note.isActive = false; });
      notesList.push(new NotePaperModel(maxID + 1, store.state.selectedTab, '', 50, 50, 200, 300, 0, true, 1000, 1000));
    };

    const saveNote = () => {
      if (store.state.isDirty) {
        if (store.state.isLoading === false) {
          store.commit('setLoading', true);

          axios.post('/api/notes/batch-update', {
            notePaperList: notesList,
            tabID: store.state.selectedTab,
          }).then((result: any) => {
            if (!result.handledByIntercepter) {
              const myResponse = result.data;

              if (myResponse.statusCode === 200) {
                store.commit('setIsDirty', false);
                store.commit('setAutoSaveCounter', 0);
                store.commit('setLoading', false);

                notesList.splice(0, notesList.length);
                myResponse.data.forEach((note: any, index: number, array: any) => {
                  const maxID = notesList.reduce(
                    (accumulator, currentValue) => (currentValue.noteNo > accumulator
                      ? currentValue.noteNo : accumulator),
                    0,
                  );
                  notesList.push(
                    new NotePaperModel(
                      maxID + 1,
                      note.tabUniqueID,
                      note.noteContent,
                      note.notePositionX,
                      note.notePositionY,
                      note.noteWidth,
                      note.noteHeight,
                      note.notePriority,
                      false,
                      note.screenWidth,
                      note.screenHeight,
                      note.notePaperUniqueID,
                    ),
                  );

                  if (index + 1 === array.length) {
                    setTimeout(() => {
                      store.commit('setIsDirty', false);
                      store.commit('setAutoSaveCounter', 0);
                    }, 200);
                  }
                });
              } else {
                store.commit('setLoading', false);
                store.commit('setShowGenericModal', true);
                store.commit('setGenericModalTitle', 'Save Failed');
                store.commit('setGenericModalMessage', myResponse.message);
                store.commit('setGenericModalCloseCallback', () => { /** */ });
              }
            }
          }).catch((err: any) => {
            store.commit('setLoading', false);
            store.commit('setShowGenericModal', true);
            store.commit('setGenericModalTitle', 'Unexpected Error');
            store.commit('setGenericModalMessage', 'An unexpected error occured. Please contact administrator.');
            store.commit('setGenericModalCloseCallback', () => { /** */ });
          });
        }
      }
    };

    let saveHeartbeat: any = null;

    onMounted(() => {
      // every 10 second check if need to save
      saveHeartbeat = setInterval(() => {
        if (store.state.autoSaveCounter > 0) {
          store.commit('setAutoSaveCounter', store.state.autoSaveCounter - 1);
        }

        if (store.state.isDirty && store.state.autoSaveCounter === 0) {
          console.log('save note');
          saveNote();
        }
      }, 1000);
    });

    onUnmounted(() => {
      clearInterval(saveHeartbeat);
    });

    return {
      notesList,
      boardBackground,
      inactiveAllNote,
      changeActive,
      addNewNote,
      saveNote,
      updateNoteModel,
      isDirty: computed(() => store.state.isDirty),
      autoSaveCounter: computed(() => store.state.autoSaveCounter),
      selectedTab: computed(() => store.state.selectedTab),
    };
  },
});
