
import {
  defineComponent,
  ref,
} from 'vue';
import NotePaperModel from '@/class/NotePaperModel';
import { debounce } from 'lodash';

export default defineComponent({
  props: {
    notesID: String,
    notesPositionX_Prop: {
      type: String,
      default: '100px',
    },
    notesPositionY_Prop: {
      type: String,
      default: '20px',
    },
    getBoardBackground: {
      type: Function,
      default: () => ({}),
    },
    noteModel: {
      type: NotePaperModel,
      default: {},
    },
  },
  setup(props:any, context:any) {
    const dragStartPositionX = ref(0);
    const dragStartPositionY = ref(0);
    const isDragging = ref(false);
    const notesText = ref();
    const notesContainer: any = ref(null);
    const resizeIcon: any = ref(null);

    const dragStart = (e: UIEvent) => {
      // get the mouse cursor position at startup:
      dragStartPositionX.value = (e as MouseEvent).clientX || (e as TouchEvent).touches[0].clientX;
      dragStartPositionY.value = (e as MouseEvent).clientY || (e as TouchEvent).touches[0].clientY;
      isDragging.value = true;
    };

    const dragging = (e: UIEvent) => {
      if (isDragging.value === true) {
        const pos1 = dragStartPositionX.value - ((e as MouseEvent).clientX
        || ((e as TouchEvent).touches ? (e as TouchEvent).touches[0].clientX : 0));
        const pos2 = dragStartPositionY.value - ((e as MouseEvent).clientY
        || ((e as TouchEvent).touches ? (e as TouchEvent).touches[0].clientY : 0));
        dragStartPositionX.value = ((e as MouseEvent).clientX
        || ((e as TouchEvent).touches ? (e as TouchEvent).touches[0].clientX : 0));
        dragStartPositionY.value = ((e as MouseEvent).clientY
        || ((e as TouchEvent).touches ? (e as TouchEvent).touches[0].clientY : 0));

        const myNotesContainer: HTMLElement = notesContainer.value as HTMLElement;
        const myRemoveIcon: HTMLElement = resizeIcon.value as HTMLElement;
        const boardBackground: HTMLElement = props.getBoardBackground();
        let finalXPosition = (myNotesContainer.offsetLeft - pos1);
        let finalYPosition = (myNotesContainer.offsetTop - pos2);
        const maxXPosition = boardBackground.clientWidth - myNotesContainer.clientWidth;
        const maxYPosition = boardBackground.clientHeight - myNotesContainer.clientHeight;
        finalXPosition = finalXPosition < 0 ? 0 : finalXPosition;
        finalYPosition = finalYPosition < 0 - myRemoveIcon.clientHeight * 2
          ? -myRemoveIcon.clientHeight * 2 : finalYPosition;
        finalXPosition = finalXPosition > maxXPosition ? maxXPosition : finalXPosition;
        finalYPosition = finalYPosition > maxYPosition ? maxYPosition : finalYPosition;
        // eslint-disable-next-line vue/no-mutating-props, no-param-reassign
        props.noteModel.notePositionX = finalXPosition;
        // eslint-disable-next-line vue/no-mutating-props, no-param-reassign
        const myNoteModel = props.noteModel;
        myNoteModel.notePositionY = finalYPosition;
        myNoteModel.notePositionX = finalXPosition;
        context.emit('update:noteModel', myNoteModel);
      }
    };

    const dragStop = (e: UIEvent) => {
      isDragging.value = false;
    };

    const addPriority = (e: UIEvent) => {
      // get the mouse cursor position at startup:
      // eslint-disable-next-line vue/no-mutating-props, no-param-reassign
      props.noteModel.notePriority += 1;
    };

    const minusPriority = (e: UIEvent) => {
      // get the mouse cursor position at startup:
      if (props.noteModel.notePriority > 0) {
        // eslint-disable-next-line vue/no-mutating-props, no-param-reassign
        props.noteModel.notePriority -= 1;
      }
    };

    const updateContent = (content: string) => {
      const myNoteModel = props.noteModel;
      myNoteModel.noteContent = content;
      context.emit('update:noteModel', myNoteModel);
    };

    const onResize = (e: UIEvent) => {
      const oldWidth = props.noteModel.noteWidth;
      const oldHeight = props.noteModel.noteHeight;

      const myNotesTextArea: HTMLElement = notesText.value as HTMLElement;
      const newWidth = myNotesTextArea.clientWidth;
      const newHeight = myNotesTextArea.clientHeight;

      console.log({
        oldWidth,
        newWidth,
        oldHeight,
        newHeight,
      });

      if (oldWidth !== newWidth || oldHeight !== newHeight) {
        const myNoteModel = props.noteModel;
        myNoteModel.noteWidth = newWidth;
        myNoteModel.noteHeight = newHeight;
        context.emit('update:noteModel', myNoteModel);
        console.log('update note model');
      }
    };

    // Listener code
    window.addEventListener('mouseup', () => { isDragging.value = false; }, false);

    return {
      dragStartPositionX,
      dragStartPositionY,
      isDragging,
      notesText,
      notesContainer,
      resizeIcon,
      dragStart,
      dragging,
      dragStop,
      addPriority,
      minusPriority,
      updateContent,
      onResize,
    };
  },
});

