
import { defineComponent } from 'vue';
import { debounce } from 'lodash';

export default defineComponent({
  props: {
    tabName: String,
    isActive: Boolean,
  },
  setup(_, { emit }) {
    const onInput = debounce((e: any) => {
      emit('update:tabName', (e?.target as HTMLInputElement).value);
    }, 1000);

    return {
      onInput,
    };
  },
});

