import { createApp } from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import store from './store';
import App from './App.vue';
import router from './router';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';

function getCookie(cname: string) {
  let c;
  const name = `${cname}=`;
  const decodedCookie = decodeURIComponent(document.cookie);
  const ca = decodedCookie.split(';');
  for (let i = 0; i < ca.length; i += 1) {
    c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return '';
}
console.log(getCookie('Authorization'));

const app = createApp(App);
app.use(router);
app.use(store);
app.use(VueAxios, axios);
app.provide('axios', app.config.globalProperties.axios);
app.mount('#app');

axios.defaults.baseURL = 'https://notesanywhere-backend.onrender.com';
// axios.defaults.baseURL = 'http://localhost:8080';
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.common.Authorization = getCookie('Authorization');
axios.defaults.withCredentials = true;

axios.interceptors.response.use(
  (response: any) => {
    // Do something with the successful response
    if (response.data.statusCode === 401) {
      store.commit('setLoading', false);
      store.commit('setShowGenericModal', true);
      store.commit('setGenericModalTitle', 'Session Expired');
      store.commit('setGenericModalMessage', 'Please proceed to login again.');
      store.commit('setGenericModalCloseCallback', () => {
        router.push({ path: '/NotesAnywhere/' });
      });
      // eslint-disable-next-line no-param-reassign
      response.handledByIntercepter = true;
    }
    return response;
  },
  (error:any) => Promise.reject(error),
);
