
import { defineComponent, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  setup() {
    const store = useStore();

    const closeModal = () => {
      store.commit('setShowGenericModal', false);
      store.commit('setGenericModalMessage', '');
      store.commit('setGenericModalTitle', '');
      store.state.genericModalCloseCallback();
    };

    return {
      showGenericModal: computed(() => store.state.showGenericModal),
      genericModalTitle: computed(() => store.state.genericModalTitle),
      genericModalMessage: computed(() => store.state.genericModalMessage),
      closeModal,
    };
  },
});
