class NotePaperModel {
    notePaperUniqueID?: string;

    noteNo: number;

    tabUniqueID: string;

    noteContent: string;

    notePositionX: number;

    notePositionY: number;

    noteWidth: number;

    noteHeight: number;

    isActive: boolean;

    notePriority: number;

    screenWidth: number;

    screenHeight: number;

    constructor(
      noteNo: number,
      tabUniqueID: string,
      noteContent: string,
      notePositionX: number,
      notePositionY: number,
      noteWidth: number,
      noteHeight: number,
      notePriority: number,
      isActive: boolean,
      screenWidth: number,
      screenHeight: number,
      notePaperUniqueID?: string,
    ) {
      this.noteNo = noteNo;
      this.tabUniqueID = tabUniqueID;
      this.noteContent = noteContent;
      this.notePositionX = notePositionX;
      this.notePositionY = notePositionY;
      this.noteWidth = noteWidth;
      this.noteHeight = noteHeight;
      this.notePriority = notePriority;
      this.isActive = isActive;
      this.screenWidth = screenWidth;
      this.screenHeight = screenHeight;
      this.notePaperUniqueID = notePaperUniqueID;
    }

    getPositionXWithSuffix() {
      return `${this.notePositionX}px`;
    }

    getPositionYWithSuffix() {
      return `${this.notePositionY}px`;
    }

    getWidthWithSuffix() {
      return `${this.noteWidth}px`;
    }

    getHeightWithSuffix() {
      return `${this.noteHeight}px`;
    }
}

export default NotePaperModel;
