
import AppHeader from '@/components/AppHeader.vue'; // @ is an alias to /src

import {
  defineComponent,
  inject,
  ref,
} from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default defineComponent({
  components: {
    AppHeader,
  },
  setup() {
    const axios: any = inject('axios');
    const store = useStore();
    const router = useRouter();

    const email = ref('mun-ji@hotmail.com');
    const password = ref('asd@1234');
    const emailBlured = ref(false);
    const passwordBlured = ref(false);
    const valid = ref(false);
    const submitted = ref(false);

    const validEmail = (emailText: string) => {
      const re = /(.+)@(.+){2,}\.(.+){2,}/;
      return re.test(emailText.toLowerCase());
    };

    const validPassword = (pass: string) => pass.length > 0;

    const validate = () => {
      emailBlured.value = true;
      passwordBlured.value = true;
      if (validEmail(email.value) && validPassword(password.value)) {
        valid.value = true;
      }
    };

    const submit = () => {
      validate();
      if (valid.value) {
        submitted.value = true;
        store.commit('setLoading', true);

        axios.post('/api/user/login', {
          email: email.value,
          password: password.value,
        }).then((result: any) => {
          if (!result.handledByIntercepter) {
            const myResponse = result.data;
            store.commit('setLoading', false);

            if (myResponse.statusCode === 200) {
              const jwtToken = myResponse.data;
              axios.defaults.headers.common.Authorization = `Bearer ${jwtToken}`;
              store.commit('setShowGenericModal', true);
              store.commit('setGenericModalTitle', 'Login Successful');
              store.commit('setGenericModalMessage', 'Welcome back.');
              store.commit('setGenericModalCloseCallback', () => {
                router.push({ path: '/NotesAnywhere/dashboard' });
              });
            } else {
              store.commit('setShowGenericModal', true);
              store.commit('setGenericModalTitle', 'Register Failed');
              store.commit('setGenericModalMessage', myResponse.message);
              store.commit('setGenericModalCloseCallback', () => { /** */ });
            }
          }
        }).catch((err: any) => {
          store.commit('setLoading', false);
        });
      }
    };

    return {
      email,
      password,
      emailBlured,
      passwordBlured,
      valid,
      submitted,
      validEmail,
      validPassword,
      submit,
    };
  },
});

