import axios from 'axios';

class NoteTabModel {
    tabUniqueID?: string;

    tabNo: number;

    tabName: string;

    isActive: boolean;

    constructor(tabNo: number, tabName: string, isActive: boolean, tabUniqueID?: string) {
      this.tabUniqueID = tabUniqueID;
      this.tabNo = tabNo;
      this.tabName = tabName;
      this.isActive = isActive;
    }

    save() : Promise<any> {
      return new Promise((resolve, reject) => {
        const data = {
          tabUniqueID: this.tabUniqueID,
          tabNo: this.tabNo,
          tabName: this.tabName,
          isActive: this.isActive,
        };
        if (this.tabUniqueID == null) {
          axios.post('/api/tab/create', data).then((result: any) => {
            if (!result.handledByIntercepter) {
              const serverResponse = result.data;
              const tabObjectSaved = serverResponse.data;
              this.tabUniqueID = tabObjectSaved.tabUniqueID;
              this.tabName = tabObjectSaved.tabName;
              this.isActive = tabObjectSaved.isActive;

              if (serverResponse.statusCode === 200) {
                resolve({ success: true });
              } else {
                resolve({ success: false, message: '' });
              }
            }
          }).catch((err) => {
            reject(err);
          });
        } else {
          axios.post('/api/tab/update', data).then((result: any) => {
            if (!result.handledByIntercepter) {
              const serverResponse = result.data;

              if (serverResponse.statusCode === 200) {
                resolve({ success: true });
              } else {
                resolve({ success: false, message: '' });
              }
            }
          }).catch((err) => {
            reject(err);
          });
        }
      });
    }

    delete() : Promise<any> {
      return new Promise((resolve, reject) => {
        axios.post(`/api/tab/delete/${this.tabUniqueID}`).then((result: any) => {
          if (!result.handledByIntercepter) {
            const serverResponse = result.data;

            if (serverResponse.statusCode === 200) {
              resolve({ success: true });
            } else {
              resolve({ success: false, message: '' });
            }
          }
        }).catch((err) => {
          reject(err);
        });
      });
    }
}

export default NoteTabModel;
