import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';
import Login from '../views/LoginView.vue';
import Register from '../views/RegisterView.vue';
import NotesDashboardView from '../views/NotesDashboardView.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: { name: 'login' },
  },
  {
    path: '/NotesAnywhere/',
    name: 'login',
    component: Login,
  },
  {
    path: '/NotesAnywhere/register',
    name: 'register',
    component: Register,
  },
  {
    path: '/NotesAnywhere/dashboard',
    name: 'dashboard',
    component: NotesDashboardView,
  },
];

const router = createRouter({
  history: createWebHistory('/NotesAnywhere/'),
  routes,
});

export default router;
