import { createStore } from 'vuex';

const store = createStore({
  state: {
    isLoading: false,
    selectedTab: '',
    isDirty: false,
    autoSaveCounter: 0,
    showGenericModal: false,
    genericModalTitle: '',
    genericModalMessage: '',
    genericModalCloseCallback: () => { /** */ },
  },
  getters: {},
  mutations: {
    setLoading(state, isLoading) {
      state.isLoading = isLoading;
    },
    setSelectedTab(state, selectedTab) {
      state.selectedTab = selectedTab;
    },
    setIsDirty(state, isDirty) {
      state.isDirty = isDirty;
    },
    setAutoSaveCounter(state, autoSaveCounter) {
      state.autoSaveCounter = autoSaveCounter;
    },
    setShowGenericModal(state, showGenericModal) {
      state.showGenericModal = showGenericModal;
    },
    setGenericModalMessage(state, genericModalMessage) {
      state.genericModalMessage = genericModalMessage;
    },
    setGenericModalTitle(state, genericModalTitle) {
      state.genericModalTitle = genericModalTitle;
    },
    setGenericModalCloseCallback(state, genericModalCloseCallback) {
      state.genericModalCloseCallback = genericModalCloseCallback;
    },
  },
  actions: {},
});

export default store;
